<template>
  <div class="shopbag">
    <van-nav-bar fixed title="购物袋" left-text="返回" @click-left="back" left-arrow :right-text="isEdit ? '完成':'管理'" @click-right="editProduct">
      <!-- <template #right>
        <van-icon name="search" size="18" />
      </template>-->
    </van-nav-bar>

    <div class="img-box">
      <img class="auto-img" src="../../assets/images/shopbag_bg.png">

    </div>

    <van-empty v-if="shopbagData.length == 0" description="你的购物袋好寂寞">
      <van-button round class="bottom-button" @click="toMenu">
        去逛逛
      </van-button>
    </van-empty>

    <div v-else>
      <div class="item-list">

        <van-swipe-cell :disabled="!isEdit" v-for="(item,index) in shopbagData" :key="index">
          <van-cell center>
            <div class="item clearfix">
              <van-checkbox class="fl" v-model="item.isCheck" @change="simpleSelect"/>
              <div class="product-info fl">
                <div class="product-img fl">
                  <img
                    class="auto-img"
                    :src="item.small_img"
                    alt
                  />
                </div>
                <div class="fl">
                  <div class="name">{{item.name}}</div>
                  <div class="rule">{{item.rule}}</div>
                </div>
              </div>
              <!-- 步进器 -->
              <van-stepper class="fr" theme="round" button-size="22" v-model="item.count" disable-input @change="modifyCount(item)"/>
              <div class="price fr">￥{{item.price}}</div>
            </div>
          </van-cell>
          <template #right>
            <van-button square type="danger" text="删除" @click="deleteSimpleShopbag(item.sid,index)"/>
          </template>
        </van-swipe-cell>

      </div>

      <van-submit-bar :price="totalPrice" button-text="提交订单" v-show="!isEdit" @submit="toPay">
        <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
      </van-submit-bar>
      <van-submit-bar button-text="删除选中" v-show="isEdit" @submit="deleteMoreShopbag">
        <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
      </van-submit-bar>
    </div>
  </div>
</template>

<script>
import "../../assets/less/shopBag.less";
import {utils} from "../../assets/js/utils"
export default {
  name: "ShopBag",
  created() {
    // // console.log("shopbag");
    this.getShopbagData();
    this.CountTotalPrice();
  },
  data() {
    return {
      allCheck: false,
      isEdit: false,
      shopbagData: [],
      totalPrice: 0,
    };
  },
  methods: {
    // 返回
    back(){
      this.$router.go(-1);
    },
    // 编辑购物袋
    editProduct(){
      this.isEdit = !this.isEdit;
    },
    // 获取购物袋数据
    getShopbagData(){
      let tk = localStorage.getItem('CSTK');
      // 不存在token
      if(!tk){
        return;
      }
      this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      duration: 0,
      loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString: tk
        }
      }).then( result => {
        // console.log("findAllShopcart result ==> ",result);
        this.$toast.clear();
        if(result.data.code == 5000){
          result.data.result.map((v) => {
            v.isCheck = false;
          })
          this.shopbagData = result.data.result;
        }
      }).catch( err => {
        this.$toast.clear();
        // console.log("findAllShopcart err ==> ",err);
      })

    },
    // 全选
    allSelect(){
      this.shopbagData.map(v => {
        v.isCheck = this.allCheck;
      })
      this.CountTotalPrice();
    },
    // 单选
    simpleSelect(){
      this.CountTotalPrice();
      for(let i = 0; i < this.shopbagData.length; i++){
        if(!this.shopbagData[i].isCheck){
          this.allCheck = false;
          return;
        }
      }
      this.allCheck = true;
      // // console.log(this.allCheck)
    },
    //删除单个
    deleteSimpleShopbag(sid,index){
      let tk = localStorage.getItem('CSTK');
      // 不存在token
      if(!tk){
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString: tk,
        sids: JSON.stringify([sid])
      })

      this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data
      }).then((result) => {
          if (result.data.code == 7000) {
            this.$toast.clear();
            this.shopbagData.splice(index,1);
            this.CountTotalPrice();
            this.$toast(result.data.msg);
          }
          // console.log("deleteShopcart result ==>", result);
        }).catch((err) => {
          this.$toast.clear();
          // console.log("deleteShopcart err ==>", err);
        });
    },
    //删除多个
    deleteMoreShopbag(){
      let tk = localStorage.getItem('CSTK');
      // 不存在token
      if(!tk){
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      let sid = [];
      this.shopbagData.map(v => {
        if(v.isCheck){
          sid.push(v.sid);
        }
      })

      // // console.log("sid ==> ",sid)

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString: tk,
        sids: JSON.stringify([sid])
      })

      this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data
      }).then((result) => {
          if (result.data.code == 7000) {
            this.$toast.clear();
            for(let i = this.shopbagData.length - 1;i >= 0 ;i--){
              if(this.shopbagData[i].isCheck){
                this.shopbagData.splice(i,1);
              }
            }
            this.CountTotalPrice();
            this.$toast(result.data.msg);
          }
          // console.log("deleteShopcart more result ==>", result);
        }).catch((err) => {
          this.$toast.clear();
          // console.log("deleteShopcart more err ==>", err);
        });
    },
    // 修改购物车数量
    modifyCount(item){
      let tk = localStorage.getItem('CSTK');
      // 不存在token
      if(!tk){
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString: tk,
        sid: item.sid,
        count: item.count
      })

      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data
      }).then(result =>{
        // console.log("modifyShopcartCount result ==> ",result)
        this.$toast.clear();
        this.CountTotalPrice();
        if(result.data.code !== 6000){
          this.$toast(result.data.msg);
        }
      }).catch( err => {
        this.$toast.clear();
        // console.log("modifyShopcartCount err ==> ",err)
      })
    },
    // 计算总价
    CountTotalPrice(){
      this.totalPrice = 0;
      this.shopbagData.map(v =>{
        if(v.isCheck){
          this.totalPrice += v.count * v.price;
        }
      })
      this.totalPrice *= 100;
    },
    // 提交订单
    toPay(){
      let sids = [];
      this.shopbagData.map(v => {
        if(v.isCheck){
          sids.push(v.sid);
        }
      })
      // 未选择商品则不跳转
      if(!sids.length){
        this.$toast("还没有选择任何商品哦~")
        return;
      }
      sids = sids.join('-');
      // console.log(sids);
      this.$router.push({name: 'Pay',query: {sids}})
    },
    // 跳转菜单页面
    toMenu(){
      this.$router.push({name: "Menu"})
    }
    
  }
};
</script>
